// import React from "react";
import styled from "styled-components";

const FlexBox = styled.div`
  display: flex;
  align-items: center;
`;

export const FlexBoxContentCenter = styled(FlexBox)`
  justify-content: center;
`;

export const FlexBoxContentLeft = styled(FlexBox)`
  justify-content: flex-start;
`;

export const FlexBoxContentRight = styled(FlexBox)`
  justify-content: flex-end;
`;
